import React from 'react'
import { Row, Col, Modal, Card, Button, Table, message } from 'antd'
import {fetchArtists} from '../../api/login.js'
import './index.scss'
const columns = (appManage) => [{
  title: '应用名称',
  dataIndex: 'name',
  render: (text, record) => (<img style={{ width: '60px', display: 'block', fontSize: '0px' }} src={record.img} />),
}, {
  dataIndex: 'age',
  render: (text, record) => (
    <span>
      <h3>{record.name}</h3>
      <div>包名:{record.packageName}</div>
      <div>版本:{record.version}</div>
    </span>
  ),
}, {
  title: '应用类型',
  dataIndex: 'type',
}, {
  title: '状态',
  dataIndex: 'status',
}, {
  title: '操作',
  render: (text, record) => (<Button onClick={() => appManage(record)}>应用管理</Button>)
}];
