class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      user: '123',
      money: '0.00',
      tableLoading: false,
      selectedRowKeys: [],
    }
    this.appManage = this.appManage.bind(this)
  }
  componentWillMount(){
    this.fetchArtistss()
  }
  
  fetchArtistss (){
    try {
      fetchArtists().then(res=>{
        console.log(res)
      })
    } catch (error) {
      console.log(error)
      message.error('error');
    }
  }
  onSelectChange = (selectedRowKeys) => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    this.setState({ selectedRowKeys });
  }
  appManage(v) {
    console.log(v)
    message.success('This is a message of success');
  }
  render() {
    const { tableLoading, selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <div style={styles.bg} className='home'>
        <div className='info'>
          <Row >
            <Col span={14}>
              <Card
                title="开发者信息"
                extra={<em href="#">编辑</em>}
              >
                <p>欢迎回来，</p>
                <h1>北京三金宇通爱上电视</h1>
                <p>上次登录时间{this.state.user}</p>
              </Card>
            </Col>
            <Col span={10}>
              <Card
                title="财务情况"
                className='money ml20'
                extra={<div><em>明细</em><em style={{ marginLeft: '7px' }}>明细</em></div>}
              >
                <p>账户余额：<span>{this.state.money}</span> 元</p>
                <p>昨日消耗：<span>{this.state.money}</span> 元</p>
              </Card>
            </Col>
          </Row>
        </div>
        <div className='gameCon'>
          <div>
            <Button type="primary">添加应用</Button>
          </div>
          <div className="mt20">
            <Table loading={this.state.tableLoading} className="nopadtable" rowSelection={rowSelection} columns={columns(this.appManage)} dataSource={data} />,
          </div>
        </div>
        <Modal
          title="Modal"
          visible={this.state.visible}
          onOk={this.hideModal}
          onCancel={this.hideModal}
          okText="确认"
          cancelText="取消"
        >
          <p>Bla bla ...</p>
          <p>Bla bla ...</p>
          <p>Bla bla ...</p>
        </Modal>
      </div>
    )
  }
}

const styles = {
  bg: {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: 'calc(100vh - 64px)'
  }
}

export default Home



