import request from '../utils/request'

export function fetchArtists(params) {
  return request({
    url: '/Artists',
    method: 'get',
    params
  })
}

export function fetchToken(data) {
  return request({
    url: '/token',
    method: 'post',
    // headers: { 'Content-Type': 'application/json' },
    data
  })
}



